<template>
  <div class="masonry-layout">
    <div v-for="(item, index) in items" :key="index" class="masonry-item">
      <img :src="item.imageUrl" alt="Image"/>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        items: [
          {imageUrl: '/static/images/003.jpg'},
          {imageUrl: '/static/images/004.jpg'},
          {imageUrl: '/static/images/005.jpg'},
          {imageUrl: '/static/images/006.jpg'},
          {imageUrl: '/static/images/007.jpg'},
          {imageUrl: '/static/images/008.jpg'},
          {imageUrl: '/static/images/009.jpg'},
          {imageUrl: '/static/images/ai1.png'},
          {imageUrl: '/static/images/ai2.png'},
          {imageUrl: '/static/images/ai3.png'},
          {imageUrl: '/static/images/ai4.png'},
          {imageUrl: '/static/images/ai5.png'},
        ]
      };
    },
  };
</script>

<style scoped>
  .masonry-layout {
    /*设置列数*/
    column-count: 3;
    /*列之间的间隔*/
    column-gap: 10px;
  }

  .masonry-item {
    /*防止图片跨列*/
    break-inside: avoid;
    /*图片之间的间隔*/
    margin-bottom: 10px;
  }

  /*图片宽度占满列*/
  img {
    width: 100%;
  }
</style>



